import React, { Component } from 'react'
import { Link } from 'react-router-dom';

import { CSSTransition, TransitionGroup } from 'react-transition-group';

import { withStyles } from '@material-ui/styles';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import blue from '@material-ui/core/colors/blue';
import red from '@material-ui/core/colors/red';
import Avatar from '@material-ui/core/Avatar';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";

import MiniPalette from '../mini-palette/MiniPalette.component';
import PaletteListStyles from './PaletteList.styles';
export class PaletteList extends Component {

  state={
    openDeleteDialog: false,
    deletingId: ''
  }

  openDialog = (id) => {
    this.setState({openDeleteDialog: true, deletingId: id})
  }

  closeDialog = () => {
    this.setState({openDeleteDialog: false, deletingId: ''})

  }

  goToPalette = (id) => {
    this.props.history.push(`/palette/${id}`);
  }

  handleDelete = () => {
    this.props.deletePalette(this.state.deletingId);
    this.closeDialog();
  }

  render() {
    const { palettes, classes } = this.props;
    return (
      <div className={classes.root}>
        <div className={classes.container}>
          <nav className={classes.nav}>
            <h1 className={classes.heading}>React Colors</h1>  
            <Link to='/palette/new'>Create New Palette</Link>        
          </nav>
          
          <TransitionGroup className={classes.palettes}>
            {
              palettes.map(palette => (
                <CSSTransition key={palette.id} classNames= 'fade' timeout={500}>
                  <MiniPalette
                    key={palette.id} 
                    id={palette.id}
                    {...palette} 
                    goToPalette={this.goToPalette}
                    // deletePalette={deletePalette}
                    openDialog={this.openDialog}
                  />
                </CSSTransition>
              ))
            }
          </TransitionGroup>
          </div>
          <Dialog open={this.state.openDeleteDialog} aria-labelledby='delete-dialog-title' onClose={this.closeDialog}>
            <DialogTitle id="delete-dialog-title">Delete This Palette</DialogTitle>
            <ListItem button onClick={this.handleDelete}>
              <ListItemAvatar>
                <Avatar style={{backgroundColor: blue[100], color: blue[600]}}>
                  <CheckIcon />
                </Avatar>
              </ListItemAvatar>
              <ListItemText primary='Delete'/>
            </ListItem>
            <ListItem button onClick={this.closeDialog}>
              <ListItemAvatar>
                <Avatar style={{backgroundColor: red[100], color: red[600]}}>
                  <CloseIcon />
                </Avatar>
              </ListItemAvatar>
              <ListItemText primary='Cancel'/>
            </ListItem>
          </Dialog>
        </div>
    )
  }
}

export default withStyles(PaletteListStyles)(PaletteList)

